











import listCell from '@/components/yt-list-cell';

export default {
	components: {
		listCell,
	},
	data() {
		return {
			shareTypeList: [{
				type: 1,
				icon: this.$api.getImageUrl + 'Upload/Images/share_wechat.png',
				text: '微信好友'
			},{
				type: 2,
				icon: this.$api.getImageUrl + 'Upload/Images/share_moment.png',
				text: '朋友圈'
			},{
				type: 3,
				icon: this.$api.getImageUrl + 'Upload/Images/share_qq.png',
				text: 'QQ好友'
			},{
				type: 4,
				icon: this.$api.getImageUrl + 'Upload/Images/share_qqzone.png',
				text: 'QQ空间'
			}]
		}
	},
	onLoad() {
		
	},
	methods: {
		navToMediaList() {
			uni.navigateTo({
				url: '/pages/mediaList/mediaList'
			})
		},
		navToDetailPage() {
			uni.navigateTo({
				url: '/pages/detail/detail'
			})
		},
		//遮罩层
		toggleMask(){
			this.$refs.mask.toggleMask();
		},
		//遮罩层底部按钮点击
		maskBottomClick(){
			this.$api.msg('点击了取消平按钮');
			this.$refs.mask.toggleMask();
		},
		shareToFriend(type){
			const typeText = ['微信好友', '朋友圈', 'QQ好友', 'QQ空间'][type - 1];
			this.$api.msg('分享给' + typeText);
			this.$refs.mask.toggleMask();
		},
		navToUpload(){
			uni.navigateTo({
				url: '/pages/upload/upload'
			})
		}
	},
	//处理遮罩层物理返回键
	onBackPress(){
		if(this.$refs.mask.show){
			this.$refs.mask.toggleMask();
			return true;
		}
	}
}
